/* Login.css */
.login-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid hwb(346 5% 34%);
    border-radius: 5px;
    background-color: hwb(154 7% 26%);
  }
  
  h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  label {
    margin-bottom: 5px;
  }
  
  input {
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid hsl(126, 62%, 43%);
    border-radius: 3px;
  }
  
  button {
    padding: 10px;
    background-color: rgb(65, 35, 233);
    color: hsl(171, 85%, 34%);
    border: none;
    border-radius: 3px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: hsl(272, 100%, 35%);
  }